.footer-bottom-nav {
	&__list {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: wrap;
	}
	&__item {
		margin-right: 34px;
		&:last-child {
			margin-right: 0;
		}
	}
	&__link {
		text-decoration: none;
		.bold();
		.font-size(15);
		color: white;
		line-height: 1.6;
		&:hover {
			text-decoration: underline;
		}
	}
}