.main-slider {
	height: 262px;
	&__wrap {
		height: 100%;
	}
	&__slide {
		height: 100%;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 50% 50%;

		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	&__title {
		.fox();
		.font-size(55);
		line-height: 1.33;
		letter-spacing: 0.7px;
		text-align: left;
		color: #ff3a1e;
		position: absolute;
		bottom: 36px;
		align-self: flex-start;
		margin-left: 143px;
	}
	&__arrow {
		position: absolute;
		top: 50%;
		display: block;
		z-index: 2;
		cursor: pointer;
		transition: all .2s ease;
		&_prev {
			left: 45px;
			margin-top: -17px;
			.svg-icon-param("icon-prev", 20px, 34px, "[fill]: #ff3a1e;");
			&:hover {
				.svg-icon-param("icon-prev", 20px, 34px, "[fill]: #d43a24;");
			}
			&:active {
				.svg-icon-param("icon-prev", 20px, 34px, "[fill]: #a22311;");
			}
		}
		&_next {
			right: 45px;
			margin-top: -17px;
			.svg-icon-param("icon-next", 20px, 34px, "[fill]: #ff3a1e;");
			&:hover {
				.svg-icon-param("icon-next", 20px, 34px, "[fill]: #d43a24;");
			}
			&:active {
				.svg-icon-param("icon-next", 20px, 34px, "[fill]: #a22311;");
			}
		}
	}

	.btn {
		align-self: flex-end;
		margin-right: 176px;
	}
}