.header-nav {
	margin-right: 67px;
	&__list {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: wrap;
	}
	&__item {
		margin: 0 15px;
		&:first-child {
			margin-left: 0;
		}
		&:last-child {
			margin-right: 0;
		}
	}
	&__link {
		display: block;
		padding: 2px 0;
		text-transform: uppercase;
		text-decoration: none;
		.dinpro();
		.font-size(12);
		letter-spacing: 1px;
		color: #000000;
		transition: all .2s ease;
		border-bottom: 2px solid transparent;
		&:hover {
			color: #ff3a1e;
			border-color: #ff3a1e;
		}
	}
}