.header {
	background: white;
	&__inner-wrap {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: flex-start;
		flex-wrap: wrap;
	}
	&__logo-wrap {
		padding: 34px 40px 34px 0;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		flex-wrap: wrap;
	}
	&__logo-link {
		margin-right: 40px;
		text-decoration: none;
	}
	&__logo {
		max-width: 164px;
	}
	&__logo-descr {
		.dinpro();
		.font-size(11);
		opacity: .6;
		text-transform: uppercase;
		line-height: 1.45;
		letter-spacing: 0.8px;
		color: #6a6a6a;
		align-self: center;
	}
	&__nav-wrap {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding-left: 23px;
		position: relative;
		&:before {
			content: "";
			width: 2px;
			height: 96px;
			background: #ff3a1e;
			position: absolute;
			top: 0;
			left: 0;
		}
	}
	&__top {
		margin-bottom: 24px;
	}
	&__bottom {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: baseline;
		flex-wrap: wrap;
	}
	&__slogan {
		max-width: 193px;
	}
}